@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
* {
    font-family: 'Roboto', sans-serif;
    padding: 0;
    margin: 0;
}

h1 {
    text-align: center;
}
.app {
    background-color: #F5F5F5;
    color: #2E3131;
}
.main {
    height: 100vh;
    box-sizing: border-box;
}





.home {
    height: 100%;
    background-image: url(/swn/static/media/Reynisfjara.7bf5dcda.jpg);
    background-size: cover;
    background-position: center;
}
.nouns {
}

.verbs {

}

.header {
    position: absolute;
    width: 100%;
}
.navigationBar {
    background-color: #2E3131;
    height: 50px;
    width: 100%;
}

.navigationBar ul {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.navigationBar li {
    padding: 0 16px;
    list-style: none;
}
.navigationBar a {
    width: 60px;
    height: 30px;
    box-sizing: border-box;
    display: block;
    color: #F5F5F5;
    text-align: center;
    text-decoration: none;
    font-size: 20px;
}
.navigationBar a.active {
    border-bottom: 2px solid #F5F5F5;
}
